import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ChildDetailsPage } from '../child-details/child-details';
import { HttpClient } from '@angular/common/http';
import { isArray } from 'ionic-angular/umd/util/util';

@Component({
  selector: 'page-home-user-flow',
  templateUrl: 'home-user-flow.html',
})
export class HomeUserFlowPage {
  parentName: any[] = [];
  parentDetailsResponse: any

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public httpClient: HttpClient
  ) {
  }


  ionViewDidLoad() {
    this.getParentDetails();
  }

  getParentDetails() {
    const request = {
      "id": "open-saber.registry.search",
      "ver": "1.0",
      "ets": "11234",
      "params":
      {
        "did": "",
        "key": "",
        "msgid": ""
      },

      "request":
      {
        "Parent": {
        }
      }
    };

    const header = {
      headers: {
        'x-authenticated-user-token': 'eyJhbGciOiJSUzI1NiIsInR5cCIgOiAiSldUIiwia2lkIiA6ICJ1WXhXdE4tZzRfMld5MG5PS1ZoaE5hU0gtM2lSSjdXU25ibFlwVVU0TFRrIn0..V6EoG9i857Lx44Xo09uSa2bZSC8O89VG4Mu0QcO38G3krObPf118gNQ-N9OPbhFFP8V9Pb37BV7xDjm5utmec4gQaPwvqeA6D6m6x_MzTXfyBXZ-wG1tf_yAW8I0WSSARzKS0OankTYFPnUgMO0Zq89W0Qac89En9OPeM8VTk68INMOBp5Xj4h3SAhhGbC5DFxkDphY2fr5EMlRhmhkKRGJbqr3gJeqpeSmLtMrbt6DbapbkE7NKZGDCR_15hNuVSn1OQ33iCx5rZe7VZO4lKr-iVoAo8o6lcsNvG1AayBFPg4TqujjKHajZChQxE9BhWYj8yYYfyijqpz4I74Wtrg',
        "Access-Control-Allow-Origin": "*"
      }
    };
    this.httpClient.post("http://52.172.187.3:8080/search",
      request, header)
      .subscribe((data: any) => {
        console.log('data is', data);
        this.parentDetailsResponse = data.result;
        console.log(this.parentDetailsResponse);
        this.parentDetailsResponse.forEach(element => {
          this.parentName.push(element.name);
        });
      }, error => {
        console.log(error);
      });
  }

  navigateToChildDetailsPage(index) {
    console.log(typeof(this.parentDetailsResponse[index].children));
    this.navCtrl.push(ChildDetailsPage, {
      children: this.parentDetailsResponse[index].children
    });
  }

}



